import React, { Component } from 'react'

export default class DuaWrapper extends Component {
    state = {
        duas: [
            {
                id:undefined,
                title: undefined,
                body: undefined,
                category: undefined,
                source: undefined,
                chain: undefined
            }
        ]
    }

    onHandleChange(e) {
        const value = e.target.elements.title.value;
        this.setState({
            title : value
        })
        console.log(value);
    }

    render() {
        const duas = this.state.duas;
        return (
            <div>
                <FormComponent/>
            </div>
        )
    }
}

const FormComponent = (props) => {
    return (
        <form >
            <input type="text" name="title" onChange={this.onHandleChange}/>
        </form>
    );
};





